import { useState } from 'react';
import { useAccount, useWriteContract, useWaitForTransactionReceipt } from 'wagmi';
import { usePickaxeLevelConfig } from '@/hooks/usePickaxe';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/Card';
import { Button } from '@/components/Button';
import { getContractAddresses } from '@/contracts/addresses';
import { PICKAXE_NFT_ABI } from '@/contracts/abis';
import { formatBigInt } from '@/utils/helpers';
import { Hammer, Zap, Shield, Star } from 'lucide-react';
import { useChainId } from 'wagmi';

const LEVELS = [1, 2, 3, 4, 5];

export function MintPage() {
  const { address } = useAccount();
  const chainId = useChainId();
  const addresses = getContractAddresses(chainId);

  const [selectedLevel, setSelectedLevel] = useState(1);

  // 
  const { writeContract, data: hash } = useWriteContract();
  const { isLoading: isMinting, isSuccess } = useWaitForTransactionReceipt({ hash });

  // 
  const handleMint = async (level: number, price: bigint) => {
    if (!address) return;

    writeContract({
      address: addresses.pickaxeNFT,
      abi: PICKAXE_NFT_ABI,
      functionName: 'mintPickaxe',
      args: [level],
      value: price,
    });
  };

  if (!address) {
    return (
      <div className="container mx-auto px-4 py-20 text-center">
        <Hammer className="w-16 h-16 text-gray-400 mx-auto mb-4" />
        <h2 className="text-2xl font-bold text-gray-900 mb-2"></h2>
        <p className="text-gray-600"></p>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gradient-to-b from-slate-900 via-slate-800 to-slate-900 relative overflow-hidden">
      {/*  */}
      <div className="absolute inset-0 bg-mine-texture opacity-5" />
      <div className="absolute top-20 left-10 w-72 h-72 bg-primary-500/10 rounded-full blur-3xl animate-pulse" />
      <div className="absolute bottom-20 right-10 w-96 h-96 bg-purple-500/10 rounded-full blur-3xl animate-pulse animation-delay-300" />

      <div className="container mx-auto px-4 py-12 relative z-10">
        {/*  -  */}
        <div className="mb-12 text-center animate-fadeIn">
          <h1 className="text-5xl font-bold text-white mb-4 flex items-center justify-center gap-3">
            <Hammer className="w-12 h-12 text-yellow-400 animate-bounce" />
            
          </h1>
          <p className="text-xl text-gray-300"> ETH  NFT</p>
          <div className="mt-4 inline-block px-6 py-2 bg-white/10 backdrop-blur-md rounded-full border border-white/20">
            <p className="text-sm text-gray-300">
              ,,
            </p>
          </div>
        </div>

        {/*  -  */}
        {isSuccess && (
          <div className="mb-8 p-6 bg-gradient-to-r from-green-500 to-emerald-600 rounded-2xl shadow-2xl
                         animate-celebrate border-2 border-green-300">
            <p className="text-white font-bold text-lg text-center flex items-center justify-center gap-2">
              <span className="text-2xl animate-bounce"></span>
              !
              <span className="text-2xl animate-bounce animation-delay-200"></span>
            </p>
          </div>
        )}

        {/*  -  */}
        <div className="grid md:grid-cols-2 lg:grid-cols-3 xl:grid-cols-5 gap-6 mb-12">
          {LEVELS.map((level, index) => (
            <div
              key={level}
              className="animate-scaleIn"
              style={{ animationDelay: `${index * 100}ms` }}
            >
              <LevelCard
                level={level}
                selected={selectedLevel === level}
                onSelect={() => setSelectedLevel(level)}
                onMint={handleMint}
                minting={isMinting && selectedLevel === level}
              />
            </div>
          ))}
        </div>

      <div className="mt-12 max-w-4xl mx-auto animate-fadeIn animation-delay-500">
        <Card variant="glass" className="backdrop-blur-xl border-2 border-white/20">
          <CardHeader>
            <CardTitle className="text-2xl text-white flex items-center gap-2">
              <span className="text-3xl"></span>
              
            </CardTitle>
          </CardHeader>
          <CardContent>
            <div className="space-y-6 text-sm text-gray-200">
              <div className="p-4 bg-white/5 rounded-xl border border-white/10">
                <h4 className="font-bold text-white mb-3 flex items-center gap-2">
                  <Shield className="w-5 h-5 text-blue-400" />
                  
                </h4>
                <ul className="space-y-2">
                  <li className="flex items-start gap-2">
                    <span className="text-yellow-400 font-bold"></span>
                    <span><strong className="text-white"></strong>: ()</span>
                  </li>
                  <li className="flex items-start gap-2">
                    <span className="text-yellow-400 font-bold"></span>
                    <span><strong className="text-white"></strong>:  10 , 0 </span>
                  </li>
                  <li className="flex items-start gap-2">
                    <span className="text-yellow-400 font-bold"></span>
                    <span><strong className="text-white"></strong>: </span>
                  </li>
                  <li className="flex items-start gap-2">
                    <span className="text-yellow-400 font-bold"></span>
                    <span><strong className="text-white"></strong>: ,</span>
                  </li>
                </ul>
              </div>

              <div className="p-4 bg-gradient-to-r from-purple-900/30 to-pink-900/30 rounded-xl border border-purple-500/30">
                <h4 className="font-bold text-white mb-3 flex items-center gap-2">
                  <Star className="w-5 h-5 text-purple-400 animate-glowPulse" />
                  
                </h4>
                <div className="grid grid-cols-5 gap-3 text-center">
                  {[
                    { level: 1, prob: '1%', color: 'from-gray-400 to-gray-600' },
                    { level: 2, prob: '3%', color: 'from-green-400 to-green-600' },
                    { level: 3, prob: '5%', color: 'from-blue-400 to-blue-600' },
                    { level: 4, prob: '10%', color: 'from-purple-400 to-purple-600' },
                    { level: 5, prob: '20%', color: 'from-yellow-400 to-yellow-600' },
                  ].map((item) => (
                    <div
                      key={item.level}
                      className={`p-3 rounded-lg bg-gradient-to-br ${item.color} text-white shadow-lg`}
                    >
                      <div className="font-bold text-lg">Lv.{item.level}</div>
                      <div className="text-sm mt-1">{item.prob} </div>
                    </div>
                  ))}
                </div>
              </div>

              <div className="p-4 bg-white/5 rounded-xl border border-white/10">
                <h4 className="font-bold text-white mb-3 flex items-center gap-2">
                  <span className="text-2xl"></span>
                  
                </h4>
                <ul className="space-y-2">
                  <li className="flex items-start gap-2">
                    <span className="text-yellow-400 font-bold"></span>
                    <span> NFT,</span>
                  </li>
                  <li className="flex items-start gap-2">
                    <span className="text-yellow-400 font-bold"></span>
                    <span>,</span>
                  </li>
                  <li className="flex items-start gap-2">
                    <span className="text-yellow-400 font-bold"></span>
                    <span> GOLD (30% )</span>
                  </li>
                  <li className="flex items-start gap-2">
                    <span className="text-yellow-400 font-bold"></span>
                    <span> FHE ,</span>
                  </li>
                </ul>
              </div>
            </div>
          </CardContent>
        </Card>
      </div>
      </div>
    </div>
  );
}

// 
function LevelCard({
  level,
  selected,
  onSelect,
  onMint,
  minting,
}: {
  level: number;
  selected: boolean;
  onSelect: () => void;
  onMint: (level: number, price: bigint) => void;
  minting: boolean;
}) {
  const { data: config } = usePickaxeLevelConfig(level);

  if (!config) {
    return (
      <div className="animate-pulse bg-gradient-to-br from-gray-700 to-gray-800 rounded-2xl h-96
                     border-2 border-gray-600" />
    );
  }

  const [price, durabilityMax, efficiencyMin, efficiencyMax, luckMin, luckMax] = config as [bigint, bigint, bigint, bigint, bigint, bigint];

  const getLevelColor = (lvl: number) => {
    switch (lvl) {
      case 1: return 'from-gray-400 to-gray-600';
      case 2: return 'from-green-400 to-green-600';
      case 3: return 'from-blue-400 to-blue-600';
      case 4: return 'from-purple-400 to-purple-600';
      case 5: return 'from-yellow-400 via-yellow-500 to-yellow-600';
      default: return 'from-gray-400 to-gray-600';
    }
  };

  const getLevelName = (lvl: number) => {
    switch (lvl) {
      case 1: return '';
      case 2: return '';
      case 3: return '';
      case 4: return '';
      case 5: return '';
      default: return '';
    }
  };

  const getLevelBorderColor = (lvl: number) => {
    switch (lvl) {
      case 1: return 'border-gray-500';
      case 2: return 'border-green-500';
      case 3: return 'border-blue-500';
      case 4: return 'border-purple-500';
      case 5: return 'border-yellow-500';
      default: return 'border-gray-500';
    }
  };

  const gradientColor = getLevelColor(level);
  const borderColor = getLevelBorderColor(level);

  return (
    <div
      onClick={onSelect}
      className={`
        relative cursor-pointer transition-all duration-300 ease-out
        ${selected ? 'scale-105 z-10' : 'hover:scale-102'}
        group
      `}
    >
      <Card
        variant="dark"
        hover3d
        className={`
          border-2 ${borderColor}
          ${selected
            ? `ring-4 ring-offset-2 ring-offset-slate-900 shadow-2xl ${
                level === 5 ? 'ring-yellow-400 animate-borderGlow' : 'ring-current'
              }`
            : 'hover:shadow-2xl'
          }
          ${level >= 4 ? 'shine-effect' : ''}
          relative overflow-hidden
        `}
      >
        {/*  */}
        <div className={`
          absolute -top-16 -right-16 w-32 h-32 rounded-full
          bg-gradient-to-br ${gradientColor}
          opacity-20 blur-2xl
          ${level >= 4 ? 'animate-glow' : ''}
          ${selected ? 'opacity-30' : ''}
        `} />

        {/*  */}
        {selected && (
          <div className="absolute top-3 right-3 z-10">
            <div className={`
              w-6 h-6 rounded-full
              bg-gradient-to-br ${gradientColor}
              flex items-center justify-center
              shadow-lg animate-scaleIn
            `}>
              <span className="text-white text-xs font-bold"></span>
            </div>
          </div>
        )}

        <CardContent className="relative z-10">
          {/*  */}
          <div className="text-center mb-6">
            <div className={`
              inline-block p-6 rounded-2xl
              bg-gradient-to-br ${gradientColor}
              shadow-2xl
              ${minting ? 'animate-mining' : 'group-hover:scale-110'}
              ${level >= 5 ? 'animate-glowPulse' : ''}
              transition-transform duration-300
            `}>
              <Hammer className="w-14 h-14 text-white drop-shadow-lg" />
            </div>
            <h3 className="text-3xl font-bold mt-4 text-white"> {level}</h3>
            <span className={`
              inline-block mt-2 px-4 py-1 rounded-full text-sm font-bold
              bg-gradient-to-r ${gradientColor}
              text-white shadow-lg
            `}>
              {getLevelName(level)}
            </span>
          </div>

          {/*  */}
          <div className="space-y-3 mb-6">
            <div className="flex items-center justify-between p-2 bg-white/5 rounded-lg
                          border border-white/10 backdrop-blur-sm">
              <div className="flex items-center gap-2">
                <Shield className="w-4 h-4 text-blue-400" />
                <span className="text-sm text-gray-300"></span>
              </div>
              <span className="text-sm font-bold text-white">{Number(durabilityMax)}</span>
            </div>

            <div className="flex items-center justify-between p-2 bg-white/5 rounded-lg
                          border border-white/10 backdrop-blur-sm">
              <div className="flex items-center gap-2">
                <Zap className="w-4 h-4 text-yellow-400" />
                <span className="text-sm text-gray-300"></span>
              </div>
              <span className="text-sm font-bold text-white">
                {Number(efficiencyMin)}-{Number(efficiencyMax)}%
              </span>
            </div>

            <div className="flex items-center justify-between p-2 bg-gradient-to-r from-purple-900/30 to-pink-900/30
                          rounded-lg border border-purple-500/30">
              <div className="flex items-center gap-2">
                <Star className="w-4 h-4 text-purple-400 animate-pulse" />
                <span className="text-sm text-gray-300"></span>
              </div>
              <span className="text-xs font-semibold text-purple-300 italic">
                {Number(luckMin)}-{Number(luckMax)} 
              </span>
            </div>
          </div>

          {/*  */}
          <div className="mb-4 p-3 bg-gradient-to-r from-blue-900/20 to-purple-900/20
                        rounded-xl border border-blue-500/30">
            <div className="flex items-center justify-between">
              <span className="text-gray-300 font-medium"></span>
              <span className="text-2xl font-bold text-white flex items-center gap-1">
                {formatBigInt(price, 18)}
                <span className="text-sm text-gray-400">ETH</span>
              </span>
            </div>
          </div>

          {/*  */}
          <Button
            variant={level >= 4 ? 'gold' : 'primary'}
            size="lg"
            className="w-full"
            onClick={(e) => {
              e.stopPropagation();
              onMint(level, price);
            }}
            loading={minting}
            disabled={minting}
            glow={level >= 5}
          >
            {minting ? (
              <>
                <Hammer className="w-4 h-4 mr-1 animate-spin" />
                ...
              </>
            ) : (
              <>
                <Hammer className="w-4 h-4 mr-1" />
                
              </>
            )}
          </Button>
        </CardContent>
      </Card>
    </div>
  );
}
